import React from 'react';
import ReactDOM from 'react-dom';
import Button from '@material-ui/core/Button';
import './index.css';

class Page extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            children: props.children,
        };
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick(action) {
        let asArray = React.Children.toArray(this.state.children);

        if (action.remove) {
            const index = this.state.children.findIndex(a => a.type.name === action.remove);
            asArray.splice(index, 1);
        }

        if (action.add) {
            asArray.push(action.add);
        }

        this.setState({children: asArray})
    }

    render() {
        const childrenWithProps = React.Children.map(this.state.children, child =>
            React.cloneElement(child, { handleClick: this.handleClick })
        );

        return (
            <div className="page">
                {childrenWithProps}
            </div>
        )
    }
}

function MidiGenes(props){
    return(
        <div>
            Implementation
        </div>
    );
}

function Projects(props){
    return (
        <div className="projects">
            Projects <br/>
            <table align="center">
                <tbody>
                    <tr>
                        <th>Name</th>
                        <th>Description</th>
                        <th>Link</th>
                    </tr>
                    <tr onClick={() => props.handleClick({
                                        remove: "Projects",
                                        add: <MidiGenes/>})}>
                        <td>Midi-Genes</td>
                        <td>Generates melodies based on an evolutionary model.</td>
                        <td><a href="https://github.com/liqMix/Midi-Genes">GitHub</a></td>
                    </tr>
                </tbody>
            </table>
            <Button variant="text"
                    onClick={() => props.handleClick({
                        remove: "Projects",
                        add: <Links />})}>Back</Button> <br />
        </div>

    );
}

const Profile = () => (
        <div className="profile">
            <img src="web-noback.png" alt='liqMix' />
            <p>liqMix</p>
        </div>
);

function Links(props){
    return (
        <div className="links">
            <p>
                <Button variant="text" href="https://www.github.com/liqMix/">GitHub</Button> <br />
                <Button variant="text" href="https://www.linkedin.com/in/liqmix/">LinkedIn</Button> <br />
                <Button variant="text"
                        onClick={() => props.handleClick({
                            remove: "Links",
                            add: <Projects />})}> Projects </Button> <br/>
            </p>
        </div>
    );
}
// ========================================

ReactDOM.render(
    <Page>
        <Profile />
        <Links />
    </Page>,
  document.getElementById('root')
);